import { addDoc, collection, doc, getDoc, updateDoc } from 'firebase/firestore';
import { firestoreDb } from './client';

export type PairingRequest = {
  id: string;
  code: string;
  status: 'pending' | 'approved' | 'rejected' | 'expired';
  createdAt: number;
  updatedAt: number;
  approvedInstanceId?: string;
};

function makeCode() {
  // human-friendly-ish code
  const a = Math.random().toString(36).slice(2, 6).toUpperCase();
  const b = Math.random().toString(36).slice(2, 6).toUpperCase();
  return `${a}-${b}`;
}

export async function createPairingRequest(workspaceId: string): Promise<{ id: string; code: string }> {
  const db = firestoreDb();
  const code = makeCode();
  const ref = await addDoc(collection(db, 'workspaces', workspaceId, 'pairingRequests'), {
    code,
    status: 'pending',
    createdAt: Date.now(),
    updatedAt: Date.now(),
  });
  return { id: ref.id, code };
}

export async function getPairingRequest(workspaceId: string, requestId: string) {
  const db = firestoreDb();
  const snap = await getDoc(doc(db, 'workspaces', workspaceId, 'pairingRequests', requestId));
  if (!snap.exists()) return null;
  return { id: snap.id, ...(snap.data() as Omit<PairingRequest, 'id'>) } as PairingRequest;
}

export async function markApproved(workspaceId: string, requestId: string, instanceId: string) {
  const db = firestoreDb();
  await updateDoc(doc(db, 'workspaces', workspaceId, 'pairingRequests', requestId), {
    status: 'approved',
    approvedInstanceId: instanceId,
    updatedAt: Date.now(),
  });
}
