import { collection, doc, getDoc, getDocs, limit, query, setDoc, updateDoc } from 'firebase/firestore';
import type { User } from 'firebase/auth';
import { firestoreDb } from './client';

function now() {
  return Date.now();
}

function wid(uid: string) {
  return `ws_${uid}`;
}

export async function ensureUserAndWorkspace(user: User) {
  const db = firestoreDb();
  const userRef = doc(db, 'users', user.uid);
  const userSnap = await getDoc(userRef);

  if (!userSnap.exists()) {
    const wsId = wid(user.uid);
    const wsRef = doc(db, 'workspaces', wsId);

    await setDoc(wsRef, {
      id: wsId,
      name: `${user.displayName ?? 'My'} Workspace`,
      createdAt: now(),
      updatedAt: now(),
    });

    await setDoc(doc(db, 'workspaces', wsId, 'members', user.uid), {
      uid: user.uid,
      role: 'owner',
      createdAt: now(),
    });

    await setDoc(userRef, {
      uid: user.uid,
      email: user.email ?? null,
      displayName: user.displayName ?? null,
      photoURL: user.photoURL ?? null,
      defaultWorkspaceId: wsId,
      createdAt: now(),
      updatedAt: now(),
    });

    return { defaultWorkspaceId: wsId };
  }

  const data = userSnap.data() as { defaultWorkspaceId?: string };
  if (!data.defaultWorkspaceId) {
    const wsId = wid(user.uid);
    await updateDoc(userRef, { defaultWorkspaceId: wsId, updatedAt: now() });
    return { defaultWorkspaceId: wsId };
  }

  return { defaultWorkspaceId: data.defaultWorkspaceId };
}

export async function workspaceHasInstance(workspaceId: string): Promise<boolean> {
  const db = firestoreDb();
  const q = query(collection(db, 'workspaces', workspaceId, 'instances'), limit(1));
  const snap = await getDocs(q);
  return !snap.empty;
}
