'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { onSnapshot, doc } from 'firebase/firestore';
import { firebaseAuth, firestoreDb } from '@/lib/firebase/client';
import { ensureUserAndWorkspace, workspaceHasInstance } from '@/lib/firebase/onboarding';
import { createPairingRequest } from '@/lib/firebase/pairing';
import { NavShell } from '@/app/components/NavShell';

export default function OnboardingPage() {
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [workspaceId, setWorkspaceId] = useState<string | null>(null);
  const [pairCode, setPairCode] = useState<string | null>(null);
  const [pairStatus, setPairStatus] = useState<string>('pending');

  useEffect(() => {
    (async () => {
      const user = firebaseAuth().currentUser;
      if (!user) {
        router.replace('/sign-in');
        return;
      }

      const { defaultWorkspaceId } = await ensureUserAndWorkspace(user);
      setWorkspaceId(defaultWorkspaceId);

      const has = await workspaceHasInstance(defaultWorkspaceId);
      if (has) {
        router.replace('/boards');
        return;
      }

      setLoading(false);
    })();
  }, [router]);

  async function generateCode() {
    if (!workspaceId) return;
    const user = firebaseAuth().currentUser;
    if (!user) {
      router.replace('/sign-in');
      return;
    }

    const { id, code } = await createPairingRequest(workspaceId);
    setPairCode(code);
    setPairStatus('pending');

    const db = firestoreDb();
    onSnapshot(doc(db, 'workspaces', workspaceId, 'pairingRequests', id), (snap) => {
      const data = snap.data() as { status?: string } | undefined;
      if (!data?.status) return;
      setPairStatus(data.status);
      if (data.status === 'approved') {
        router.replace('/boards');
      }
    });
  }

  if (loading) {
    return (
      <main className="min-h-screen flex items-center justify-center p-6">
        <div className="panel p-4 text-[color:var(--muted)]">Loading…</div>
      </main>
    );
  }

  return (
    <NavShell title="Onboarding" showNav={false}>
      <div className="panel p-5">
        <div className="text-sm text-[color:var(--muted)]">Step 1 of 1</div>
        <div className="mt-1 text-xl font-extrabold">Connect your OpenClaw instance</div>
        <div className="mt-2 text-sm text-[color:var(--muted)]">
          We’ll generate a pairing code. You’ll approve it inside your OpenClaw instance. No secrets pasted.
        </div>

        {!pairCode ? (
          <button onClick={generateCode} className="mt-4 w-full btn-primary px-4 py-3 rounded-2xl font-extrabold">
            Generate pairing code
          </button>
        ) : (
          <div className="mt-4">
            <div className="text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">Pairing code</div>
            <div className="mt-2 flex items-center gap-2">
              <div className="text-3xl font-black tracking-wider">{pairCode}</div>
              <button
                aria-label="Copy pairing code"
                title="Copy"
                onClick={async () => {
                  try {
                    await navigator.clipboard.writeText(pairCode);
                  } catch {
                    // ignore
                  }
                }}
                className="w-10 h-10 grid place-items-center rounded-xl border border-[color:var(--stroke)] bg-[rgba(22,26,34,.55)] text-[color:var(--muted)] hover:text-white"
              >
                <svg width="18" height="18" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M9 9h10v10H9V9Z" stroke="currentColor" strokeWidth="2" strokeLinejoin="round" />
                  <path
                    d="M5 15H4a1 1 0 0 1-1-1V4a1 1 0 0 1 1-1h10a1 1 0 0 1 1 1v1"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                  />
                </svg>
              </button>
            </div>
            <div className="mt-2 text-sm text-[color:var(--muted)]">
              Approve this code in OpenClaw. (Next: we’ll add the exact “Approve” flow + mark instance connected.)
            </div>
            <div className="mt-4 flex items-center justify-between">
              <div className="chip">Status: {pairStatus}</div>
              <button
                onClick={() => {
                  setPairCode(null);
                }}
                className="px-4 py-2 rounded-xl border border-[color:var(--stroke)] bg-[rgba(22,26,34,.55)] font-bold"
              >
                New code
              </button>
            </div>
          </div>
        )}
      </div>
    </NavShell>
  );
}
